@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR:wght@300;400;500;700&display=swap);
/* reset.css */

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'IBM Plex Sans KR', sans-serif;
}
li {
	list-style: none;
}
button {
	all: unset;
	cursor: pointer;
}
h1, h2, h3, h4, h5, h6 {
	margin-bottom: 0 !important;		/* bootstrap에서 자체적으로 margin-bottom을 추가하기 때문에 설정 */
}
a {
	text-decoration: none;
	color: #000;
}
pre {
	font-family: "IBM Plex Sans KR", sans-serif;
	word-break: keep-all;
	white-space: break-spaces;
}
.password_secret {
	position: absolute;
	top: 50%;
	right: 10px;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	cursor: pointer;
}
.table tr th:first-of-type {
	text-align: center;
}
.pagination .page-item {
	cursor: pointer;
}
.board-write-textarea {
	resize: none;
	height: 250px;
}
.input-error {
	border-color: #dc3545 !important; 
	box-shadow: 0 0 0 0.25rem #dc354525 !important;
}
.input-error-msg {
	color: #dc3545;
	font-weight: 500;
}
.btn-write {
	position: absolute;
	top: 50%;
	right: 0;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}
.board-list-title {
	cursor: pointer;
}
.board-list-title:hover {
	text-decoration: underline;
	color: #666;
}
.board-list-title > span {
	display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
}
.spinner-bg {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	z-index: 10;
	display: flex;
}
.spinner-bg.spinner-bg-dark {
	background-color: rgba(0, 0, 0, .7);
}
.spinner {
	margin: auto;
}
.spinner .spinner-border {
	width: 100px;
	height: 100px;
	border-width: 15px;
}
.post-content {
	border-top: 1px solid #666;
}
